import { Component } from '@angular/core';
import { NavController, ModalController } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
    task: string;
    taskList: string[] = [];
    doneTaskList: string[] = [];

  constructor(public navCtrl: NavController) {

  }

  addTask(){
  	console.log(this.task);
    if (this.task != ""){
  	   this.taskList.push(this.task);
    }
  	this.task = "";
  }

  deleteTask(del){
    console.log(del);
    var index = this.taskList.indexOf(del, 0);
      if (index > -1) {
          this.taskList.splice(index, 1);
      }
  }

  updateTask(upd){
    console.log(upd);
    this.doneTaskList.push(upd);
    var index = this.taskList.indexOf(upd, 0);
      if (index > -1) {
          this.taskList.splice(index, 1);
      }
  }

  redeclareTask(reTask){
    this.taskList.push(reTask);
    var index = this.doneTaskList.indexOf(reTask, 0);
      if (index > -1) {
          this.doneTaskList.splice(index, 1);
      }
  }

  editTask(edit){
    var index = this.taskList.indexOf(edit, 0);
  }
}



